<template>
  <v-container>
    <ApolloQuery
      :query="$options.queries.postsQuery"
      :variables="{
        pagination: {
          page_num: 0,
          limit: 20,
          type:  'REQ'
        },
      }"
      v-slot="{ result: { loading, error, data } }"
    >
      <!-- Loading -->
      <div v-if="loading" class="loading apollo">Loading...</div>

      <!-- Error -->
      <div v-else-if="error" class="error apollo">An error occurred</div>

      <!-- Result -->
      <div v-else-if="data" class="result apollo">
        <v-row>
          <Post v-for="post in data.listPosts.posts" :key="post.id" :post="post" />
        </v-row>
      </div>

      <!-- No result -->
      <div v-else class="no-result apollo">No result :(</div>
    </ApolloQuery>
  </v-container>
</template>

<script>
import Post from '@/components/posts/Post'
import postsQuery from '@/graphql/queries/posts.gql'
import { ApolloQuery } from 'vue-apollo'

export default {
  components: {
    ApolloQuery,
    // ApolloMutation,
    Post
  },
  queries: {
    postsQuery
  },
  data: () => ({
    posts: []
  })
}
</script>

